    //* Ñ€Ð°Ð·Ð½Ð¸Ñ†Ð° Ð¼ÐµÐ¶Ð´Ñƒ deadLine Ð¸ Ñ‚ÐµÐºÑƒÑ‰ÐµÐ¹ Ð´Ð°Ñ‚Ð¾Ð¹

    function getTimeRemaining(endTime) {
        const t = Date.parse(endTime) - Date.parse(new Date()),
            days = Math.floor((t / (1000 * 60 * 60 * 24))), // Ð¾Ð±Ñ‰ÐµÐµ ÐºÐ¾Ð»-Ð²Ð¾ Ð¼Ð¸Ð»Ð¸ÑÐ¸ÐºÑƒÐ½Ð´ / (Ð¼Ð¸Ð»Ð¸ÑÐµÐºÑƒÐ½Ð´ Ð² ÑÑƒÑ‚ÐºÐ°Ñ…) = ÑÐºÐ¾Ð»ÑŒÐºÐ¾ ÑÑƒÑ‚Ð¾Ðº Ð¾ÑÑ‚Ð°Ð»Ð¾ÑÑŒ Ð´Ð¾ Ñ‚ÐµÐºÑƒÑ‰ÐµÐ¹ Ð´Ð°Ñ‚Ñ‹ math.Ð¾ÐºÑ€ÑƒÐ³Ð»ÐµÐ½Ð¸Ðµ
            hours = Math.floor((t / (1000 * 60 * 60) % 24)), // Ð¾Ð±Ñ‰ÐµÐµ ÐºÐ¾Ð»-Ð²Ð¾ Ð¼Ð¸Ð»Ð¸ÑÐ¸ÐºÑƒÐ½Ð´ / (Ð¼Ð¸Ð»Ð¸ÑÐµÐºÑƒÐ½Ð´ Ð² Ñ‡Ð°ÑÐµ)
            minutes = Math.floor((t / 1000 / 60) % 60),
            seconds = Math.floor((t / 1000) % 60);

            return {
            // https://alligator.io/js/object-property-shorthand-es6/
            'total': t,
            'days': days,
            'hours': hours,
            'minutes': minutes,
            'seconds': seconds
        };
    }

    function getZero(num) {
        if (num >= 0 && num < 10) {
            return `0${num}`;
        } else {
            return num;
        }
    }

    //* Ñ„-Ñ†Ð¸Ñ ÑƒÑÑ‚Ð°Ð½Ð¾Ð²ÐºÐ¸ Ñ‚Ð°Ð¹Ð¼ÐµÑ€Ð° Ð½Ð° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñƒ
    function setClock(selector, endTime) {

        const timer = document.querySelector(selector),
            days = timer.querySelector('#days'),
            hours = timer.querySelector('#hours'),
            minutes = timer.querySelector('#minutes'),
            seconds = timer.querySelector('#seconds'),
            timeInterval = setInterval(updateClock, 1000);

        updateClock(); // ÑƒÐ±Ð¸Ñ€Ð°ÐµÑ‚ Ð±Ð°Ð³ (default date set in HTML) Ð¼Ð¸Ð³Ð°Ð½Ð¸Ñ Ð¿Ñ€Ð¸ Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ð¸ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹

        function updateClock() {
            const t = getTimeRemaining(endTime); // Ñ€Ð°ÑÑ‡ÐµÑ‚ Ð¾ÑÑ‚Ð°Ð²ÑˆÐµÐ³Ð¾ÑÑ Ð²Ñ€ÐµÐ¼ÐµÐ½Ð¸

            /* days.innerHTML = t.days;
                hours.innerHTML = t.hours;
                minutes.innerHTML = t.minutes;
                seconds.innerHTML = t.seconds; */

            days.innerHTML = getZero(t.days);
            hours.innerHTML = getZero(t.hours);
            minutes.innerHTML = getZero(t.minutes);
            seconds.innerHTML = getZero(t.seconds);


            if (t.total <= 0) {
                clearInterval(timeInterval);
                days.innerHTML = 0;
                hours.innerHTML = 0;
                minutes.innerHTML = 0;
                seconds.innerHTML = 0;
            }
        }
    }
