    class MenuCard {
        constructor(src, alt, title, descr, price, parentSelector, ...classes) {
            this.src = src;
            this.alt = alt;
            this.title = title;
            this.descr = descr;
            this.price = price;
            this.classes = classes; // Ð¿ÐµÑ€ÐµÐ´Ð°ÐµÑ‚ÑÑ Ð½ÐµÐ¸Ð·Ð²ÐµÑÑ‚Ð½Ð¾Ðµ ÐºÐ¾Ð»-Ð²Ð¾ ÐºÐ»Ð°ÑÑÐ¾Ð², ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð¼Ð¾Ð³ÑƒÑ‚ Ð±Ñ‹Ñ‚ÑŒ Ð¿Ñ€Ð¸ÑÐ²Ð¾ÐµÐ½Ñ‹ Ð² Ð±ÑƒÐ´ÑƒÑŽÑ‰ÐµÐ¼
            this.parent = document.querySelector(parentSelector); // Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŒÑÐºÐ¸Ð¹ ÑÐµÐ»ÐµÐºÑ‚Ð¾Ñ€
            this.transfer = 27;
            this.changeToUAH(); // Ð¼Ð¾Ð¶Ð½Ð¾ Ð²Ñ‹Ð·Ð²Ð°Ñ‚ÑŒ ÑÑ‚Ð¾Ñ‚ Ð¼ÐµÑ‚Ð¾Ñ‚ Ð·Ð´ÐµÑÑŒ
        }
        //* Ð¿Ð¾Ñ‚Ð¾Ð¼ Ð¸Ð´ÑƒÑ‚ Ð¼ÐµÑ‚Ð¾Ð´Ñ‹

        changeToUAH() {
            this.price = this.price * this.transfer;
        }
        //* ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÑÑ‚Ñ€ÑƒÐºÑ‚ÑƒÑ€Ñ‹ <div class="menu__item">
        //? Ð¼ÐµÑ‚Ð¾Ð´ changeToUAH(); Ð¼Ð¾Ð¶Ð½Ð¾ Ð²Ñ‹Ð·Ñ‹Ð²Ð°Ñ‚ÑŒ ÑÑ€Ð°Ð·Ñƒ Ð² ÐºÐ¾Ð½ÑÑ‚Ñ€ÑƒÐºÑ‚Ð¾Ñ€Ðµ Ð»Ð¸Ð±Ð¾ Ð² render

        render() {
            const element = document.createElement('div');
            //! ÐºÐ¾Ð³Ð´Ð° Ð·Ð°Ð±Ñ‹Ð» Ð²Ð¾ Ð²Ñ‚Ð¾Ñ€Ð¾Ð¹ ÐºÐ°Ñ€Ñ‚Ð¾Ñ‡ÐºÐµ Ð¿Ñ€Ð¾Ð¿Ð¸ÑÐ°Ñ‚ÑŒ Ð´ÐµÑ„Ð¾Ð»Ñ‚Ð½Ñ‹Ð¹ ÐºÐ»Ð°ÑÑ menu__item

            if (this.classes.length === 0) {
                this.element = 'menu__item';
                element.classList.add(this.element);
            } else {
                this.classes.forEach(ÑlassName => element.classList.add(ÑlassName));
            }

            ////this.classes.forEach(ClassName => element.classList.add(ClassName));

            element.innerHTML = `

            <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.title}</h3>
                <div class="menu__item-descr">${this.descr}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                    <div class="menu__item-cost">Ð¦ÐµÐ½Ð°:</div>
                    <div class="menu__item-total"><span>${this.price}</span> Ð³Ñ€Ð½/Ð´ÐµÐ½ÑŒ</div>
                </div>

            `;
            this.parent.append(element);
        }
    }

